

/*değişmeyenler*/
#home{
    background-color: #FAF9F9;
}

#home .arrowDownIcon{
    font-size: 2.3rem;
    padding: 0;
    margin: 0;
    position: absolute;
color: #89B0AE;
bottom: 0;    
left: 0;
width: 100%;
text-align: center;
animation: downArrowAni 1s infinite;
}
@keyframes downArrowAni{
    100%{
          opacity: 0;
    }
}



/*BAŞLANGIÇ*/
/*responsive boyuta göre değişen stiller*/
/* maximum 320px e kadar */
  @media  (min-width: 10px) { 
    #home img{
         max-width: 48%;
         border: 2px solid rgb(0, 68, 255);
    }

    #home   h1, h2, h3, span{
        font-size: 1.4rem;
    }

      #home{
            text-align: center;
        }

        .homeLeft{
            padding-top: 50px;
        }
}


  /*320px ve üstü*/
  @media  (min-width: 320px) { 
  
        #home   h1, h2, h3, span{
            font-size: 1.5rem;
        }

      
  }
  
  
  /* 576px ve üstü*/
  @media (min-width: 576px) { 
  
   }
  
  
  /*768px ve üstü*/
  @media (min-width: 768px) { 
    #home{
        text-align: start;
    }
    .homeLeft{
        padding-top: 0px;
    }

    #home img{
        max-width: 60%;
        border: 2px solid rgb(0, 68, 255);
   }
   }
  
  
  /* 992px ve üstü*/
  @media (min-width: 992px) { 
    #home   h1, h2, h3, span{
        font-size: 2rem;
    }

    #home img{
        max-width: 46%;
        border: 2px solid rgb(0, 68, 255);
   }
   }
  
  
